import React from "react";
import Subtotal from "../../components/Subtotal/Subtotal";
import { useStateValue } from "../StateProvider";

//!!!!! CSS
import "./cart.scss";

import CartProduct from "../CartProduct/CartProduct";

function Cart() {
  const [{ cart }, dispatch] = useStateValue();
  //burada sepetten ekeceimiz rnleri sergilicez
  // daha sonra id image title price ekleyip cart productta arcaz
  return (
    <div className="cart">
      <div className="cart__left">
        {cart.map((item) => (
          <CartProduct
            id={item.id}
            title={item.title}
            image={item.image}
            price={item.price}
          />
        ))}
      </div>
      <Subtotal />
    </div>
  );
}

export default Cart;
